var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"international_wrapper"},[_c('div',{staticClass:"international_content"},[_c('div',{staticClass:"international_content_first"},[_c('p',{staticClass:"international_content_desc"},[_vm._v(" Обеспечение участия лиц с ограниченными физическими возможностями в международных соревнованиях является одним из важных направлений работы РГКП «Центр спортивной подготовки для лиц с ограниченными физическими возможностями» (далее – предприятие). Высокие спортивные результаты и достижения спортсменов на международной арене способствуют укреплению положительного имиджа страны, а для спортсмена участие в международных соревнованиях — это несомненно развитие, личный опыт, что способствует дальнейшему повышению и совершенствованию спортивного мастерства. И предприятие уделяет большое внимание работе по развитию и популяризации видов спорта лиц с ограниченными физическими возможностями, их достижений, привлечению людей с инвалидностью к занятию спортом. В этих целях предприятие ведет работу по развитию отношений в области спорта со спортивными организациями (международными и республиканскими федерациями по видам спорта, физкультурно-спортивными организациями, Национальным паралимпийским комитетом Республики Казахстан, Национальным сурдлимпийским комитетом Республики Казахстан). Осуществляется взаимодействие с уполномоченным государственным органом в области спорта, другими республиканскими и местными органами, в рамках осуществления, в частности, этой деятельности. Предприятие активно работает с Национальным паралимпийским комитетом Республики Казахстан. Членство Комитета в таких международных организациях как Международный паралимпийский комитет, Азиатский паралимпийский комитет, Международная спортивная федерация колясочников и ампутантов, Международная спортивная федерация слепых, предоставляет право заявлять участие казахстанских спортсменов с ограниченными физическими возможностями в международных соревнованиях, проводимых под эгидой указанных организаций. ")])]),_c('div',{staticClass:"international_content_two"},[_c('div',{staticClass:"international_content_title"},[_vm._v(" Летние и зимние Паралимпийские игры ")]),_c('img',{staticClass:"international_content_img",attrs:{"src":require("../assets/oin.png"),"alt":""}}),_c('p',{staticClass:"international_content_desc"},[_vm._v(" Летние и зимние Паралимпийские игры являются одними из самых престижных и крупнейших международных спортивных соревнований для спортсменов с ограниченными возможностями функций опорно-двигательного аппарата, зрения и интеллекта, которые представляются национальными паралимпийскими комитетами, признанными Международным паралимпийским комитетом (МПК). МПК принадлежит исключительное право на проведение Игр, которые проводятся раз в четыре года. МПК также координирует организацию и проведение чемпионатов мира и других официальных международных соревнований по паралимпийским видам спорта.Международный паралимпийский комитет (МПК) — международная некоммерческая организация, управляющая паралимпийским движением, является одной из самых крупных организаций в паралимпийском спорте (в его составе более 200 членов: национальные паралимпийские комитеты, международные федерации, региональные организации, международные спортивные организации инвалидов). Основан 22 сентября 1989 года. ")]),_c('p',{staticClass:"international_content_desc"},[_vm._v(" Штаб-квартира МПК находится в Бонне (Германия). ")]),_c('p',{staticClass:"international_content_desc"},[_vm._v(" В настоящее время МПК утверждены 28 паралимпийских видов спорта: 22 летних и 6 зимних. ")])]),_c('div',{staticClass:"international_content_three"},[_c('div',{staticClass:"international_content_title"},[_vm._v("Летние виды спорта")]),_c('div',{staticClass:"international_content_three_items"},[_c('div',{staticClass:"international_content_three_item"},[_vm._v("Стрельба из лука")]),_c('div',{staticClass:"international_content_three_item"},[_